import { standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsDataFlowDetailService, ToolsLogFlowService } from 'src/app/sdk/user/services';
import { StandarApprovalService } from 'src/app/components/approval/standar/standar-approval.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriResignService, DirektoriPegawaiService, DirektoriPegawaiPosisiService, DirektoriVPegawaiPosisiService, DirektoriHrNotesService, DirektoriPegawaiStatusService } from 'src/app/sdk/hr/services';
import { Resign, Pegawai, V_pegawai_posisi, Hr_notes, Pegawai_status } from 'src/app/sdk/hr/models';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriResignService: DirektoriResignService,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }



    resign: Resign = {}
    pegawai: Pegawai = {}
    pegawai_posisi: V_pegawai_posisi = {}

    id_resign: string = ''

    ngOnInit() {
        this.id_resign = this.routerActive.snapshot.paramMap.get('id')
        console.log(this.id_resign)
        this.getResign();
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getResign();
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''

    getResign() {
        this.spinner.show()
        this.direktoriResignService.getDirektoriResignsId(this.id_resign).subscribe(
            response => {
                this.resign = response.data
                this.getPegawai(response.data.id_pegawai)
                this.getPegawaiPosisi(response.data.id_pegawai)
                this.getNotes(response.data.id_hr_notes)

                this.params = {
                    id: response.data.id_resign,
                    id_data_flow_detail: response.data.id_data_flow_detail,
                    id_data_flow: response.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_resign"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )

    }

    dataNotes: Hr_notes = {}
    getNotes(id_hr_notes) {
        this.direktoriHrNotesService.getDirektoriHrNotesId(id_hr_notes).subscribe(
            response => {
                this.dataNotes = response.data
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            response => {
                this.pegawai = response.data;
                this.getStatus(response.data.status_pegawai)
            }
        )
    }

    dataStatus : Pegawai_status = {}

    getStatus(id){
        this.direktoriPegawaiStatusService.getDirektoriPegawaiStatusesId(id).subscribe(
            data=> {
                this.dataStatus = data.data
            }
        )
    }

    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            response => {
                this.pegawai_posisi = response.data
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_resign })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/resign/list'])
    }

}
