import { Pegawai_master } from 'src/app/sdk/hr/models/pegawai-_master';
import { Component, OnInit } from '@angular/core';
import { DirektoriResignService, DirektoriPegawaiMasterService, DirektoriHrNotesService } from 'src/app/sdk/hr/services';
import { Resign, Hr_notes } from 'src/app/sdk/hr/models';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private direktoriResignService: DirektoriResignService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false
    data: Resign = {}

    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id')

        this.form()
        this.getData()
        this.getPreeData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'hrmanagement_resign.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    listPegawai: Pegawai_master []=[]
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data=> {
                this.listPegawai = data.data
            }
        )
    }

    listNotes: Hr_notes[] = []
    getNotes() {
        this.direktoriHrNotesService.getDirektoriHrNotes(JSON.stringify({id_pegawai: this.dataForm.value.id_pegawai})).subscribe(
            data => {
                this.listNotes = data.data
            }
        )
    }


    id: string = ''
    getData() {
        this.spinner.show()
        this.direktoriResignService.getDirektoriResignsId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "hrmanagement_resignation",
                    for_table: "erp_hr.resign",
                    for_module_id: data.data.id_resign
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Resign>
    form() {
        this.dataForm = this.fb.group<Resign>(
            {
                id_resign: new FormControl(''),
                id_user: new FormControl(''),
                tanggal_efektif: new FormControl(''),
                tanggal_pengajuan: new FormControl(''),
                keterangan: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_pegawai: new FormControl(''),
                id_hr_notes: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                status: new FormControl(''),
                id_data_flow: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                nomor: new FormControl(''),
                posisi: new FormControl('')
            }
        )
    }
    simpan() {
        this.spinner.show()

        this.data = this.dataForm.value
        this.direktoriResignService.putDirektoriResignsId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/resign/list'])
    }

}
